import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

import {
    PanelHeader,
    Group,
    ContentCard,
    Tabbar,
    TabbarItem,
    Search,
    Spacing,
    Placeholder,
    Panel,
} from '@vkontakte/vkui';

import {
    Icon28HomeOutline,
    Icon28BillheadOutline,
    Icon28Menu,
    Icon16SadFaceOutline,
} from '@vkontakte/icons';

const thematics = [
    { id: 1, name: "Алгебра", date: "Алгебра 21.04.2021", theme: "TEST", work: "Домашнее задание: 23$" },
    { id: 2, name: "Алгебра", date: "Алгебра 21.04.2021", theme: "TEST", work: "Домашнее задание: 23$" },
    { id: 3, name: "Алгебра", date: "Алгебра 21.04.2021", theme: "TEST", work: "Домашнее задание: 23$" },
    { id: 4, name: "Алгебра", date: "Алгебра 21.04.2021", theme: "TEST", work: "Домашнее задание: 23$" },
    { id: 5, name: "Алгебра", date: "Алгебра 23.04.2021", theme: "TEST", work: "Домашнее задание: 23$" }
];

class Work extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            search: ''
        }
        this.onChange = this.onChange.bind(this);
    }

    onChange(e) { this.setState({ search: e.target.value }); }

    get thematics() {
        const search = this.state.search.toLowerCase();
        return thematics.filter(({ date }) => date.toLowerCase().indexOf(search) > -1);
    }

    render() {

        return (
            <Panel id="work">
            <React.Fragment>
                <PanelHeader>Домашнее задание</PanelHeader>

                <Group>
                    <Search placeholder="Алгебра 21.04.2021" value={this.state.search} onChange={this.onChange} after={null} />

                    <Spacing />

                    {this.thematics.length > 0 && this.thematics.map(thematic =>
                        <ContentCard
                            style={{ "margin-bottom": '11px' }}
                            subtitle={thematic.date}
                            header={thematic.theme}
                            caption={thematic.work}
                            key={thematic.id}>
                        </ContentCard>
                    )}

                    {this.thematics.length === 0 && <Placeholder icon={<Icon16SadFaceOutline width={56} height={56} />}
                        header="Ничего не найдено">
                        Возможно, Вы допустили ошибку в запросе
                    </Placeholder>}
                </Group>
            </React.Fragment>
            </Panel>
        );
    }
}

export default Work;